html {
  height: 100%;
  width: 100%; }

body {
  height: 100%;
  width: 100%;
  overflow: auto;
  padding-top: 1px; }

.container {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  width: calc(100% - 20px); }
  @media (min-width: 480px) {
    .container {
      width: calc(100% - 50px); } }

.col-left {
  width: 350px; }

.col-mid {
  width: 900px; }
  .col-mid--dashboard {
    width: 100%;
    padding: 0; }
    @media (min-width: 480px) {
      .col-mid--dashboard {
        padding: 0 50px; } }

.col-right {
  width: 350px; }

.main-content {
  padding: 40px 0;
  margin-top: 50px; }

header {
  background: #fff;
  box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10; }

.header {
  padding: 10px 0; }
  .header__main-header {
    font-size: 24px;
    font-weight: bold;
    color: #db4c3f;
    cursor: pointer; }
    .header__main-header a {
      color: inherit; }
      .header__main-header a:hover {
        color: inherit; }

.pagination__action-bar {
  display: flex;
  justify-content: center;
  margin: 40px auto; }

.list__item {
  margin-bottom: 10px; }

.list--news-feed > .list__item {
  padding: 10px;
  border-bottom: 1px solid #ededed;
  cursor: pointer; }
  .list--news-feed > .list__item:hover {
    border-color: #89cff0; }
    .list--news-feed > .list__item:hover .text--header {
      color: #89cff0; }
  @media (min-width: 480px) {
    .list--news-feed > .list__item {
      padding: 15px; } }

.list--news-feed .text--secondary {
  margin-top: 5px; }

.list--comment-feed .list--comment-feed {
  border-left: 2px solid #ededed;
  margin-left: 15px; }

body {
  font-size: 14px;
  line-height: 1.2;
  font-family: Arial, Helvetica, sans-serif; }

.text--header {
  font-size: 18px;
  font-weight: bold; }

.text--secondary {
  font-size: 12px;
  color: #b3b3b3; }

.text--lg {
  font-size: 30px; }

.text--md {
  font-size: 16px; }

.text--bold {
  font-weight: bold; }

.text--center {
  text-align: center; }

.separator:after {
  content: '|';
  padding: 0 5px;
  color: #b3b3b3;
  font-weight: normal; }

.preloader-block {
  pointer-events: none; }
  .preloader-block > * {
    animation: flicker 1s infinite linear; }
  .preloader-block__content {
    width: 75%;
    height: 15px;
    background: #ededed; }
    .preloader-block__content.md {
      width: 50%; }
    .preloader-block__content.sm {
      width: 100px; }
      @media (min-width: 480px) {
        .preloader-block__content.sm {
          width: 200px; } }

@keyframes flicker {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.flex-wrapper {
  display: flex; }
  .flex-wrapper--aln-ctr {
    align-items: center; }
  .flex-wrapper--jty-ctr {
    justify-content: center; }
  .flex-wrapper--ctr {
    justify-content: center;
    align-items: center; }

.mt--5 {
  margin-top: 5px; }

.mx--30 {
  margin-top: 30px;
  margin-bottom: 30px; }

.comment-block {
  padding: 10px;
  font-size: 16px; }
  .comment-block a {
    color: #007db5; }
    .comment-block a:hover {
      color: #89cff0; }
  @media (min-width: 480px) {
    .comment-block {
      padding: 15px; } }
  .comment-block__comment {
    margin-top: 10px;
    word-wrap: break-word;
    word-break: break-word; }

.circular-index {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 1px 10px 1px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  font-size: 12px; }

.btn-right-arrow {
  background: none;
  outline: none;
  /* border: none; */
  border-top-width: 2px;
  border-right-width: 2px;
  border-color: black;
  border-left: none;
  border-bottom: none;
  width: 15px;
  height: 15px;
  transform: rotate(45deg) translate(50%); }

.btn-left-arrow {
  background: none;
  outline: none;
  /* border: none; */
  border-top-width: 2px;
  border-left-width: 2px;
  border-color: black;
  border-right: none;
  border-bottom: none;
  width: 15px;
  height: 15px;
  transform: rotate(-45deg) translate(-50%); }

button {
  background: none;
  outline: none; }

.btn--icon {
  border: none; }
  .btn--icon svg {
    width: 20px;
    height: 20px; }

.btn--pagination {
  margin-top: -5px;
  width: 20px;
  height: 20px;
  align-self: center; }
  .btn--pagination.right {
    margin-left: 20px; }
  .btn--pagination.left {
    margin-right: 20px; }

.empty-state {
  flex-direction: column; }

.loader-wrapper {
  margin-bottom: 10px; }

.loader {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 2px solid #ededed;
  border-left-color: #b3b3b3;
  animation: spin-loader 1s linear infinite; }

@keyframes spin-loader {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
